      h1 La gastronoma en la industria del turismo
    p En los ltimos aos los destinos tursticos han encontrado en la gastronoma una oportunidad para destacar y brindar un mayor valor a la experiencia turstica. Este tema se ha vuelto una poderosa motivacin para viajar, ya que el comer no solo se percibe como una necesidad, sino como una experiencia estimulante que produce memorias placenteras y duraderas.
    .row.justify-content-center(data-aos="fade-up")
      .col-lg-6
        img(src='@/assets/curso/tema1/1.png', alt='Texto que describa la imagen')
      .col-lg-6.mt-3
        p Todo ello ha influido en que la gastronoma se haya convertido en uno de los segmentos ms dinmicos y con mayor crecimiento de la industria turstica, y se debe en gran parte, a que permite a los turistas conectar con lo local de una manera ms experiencial y participativa. 
        .row.color1.justify-content-center
          .col-lg-11
            p.p-2 Todos tenemos necesidad de comer y dentro de los viajes, buscamos alimentos que puedan saciar esa necesidad; sin embargo, no todos los turistas buscan viajes donde la gastronoma influya decisivamente en la eleccin del destino; por tanto, este tipo de turismo es aquel donde el visitante, realmente desea saciar su apetito hednico y cultural, ms all de lo fisiolgico; es aquel basado en la intencin de conocer, aprender, degustar y disfrutar ante todo de la cultura gastronmica del destino.
    .row.mt-5(data-aos="fade-up")
      .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mt-4.ttt
