        .bloque-texto-g__texto.p-5
          h4 Turismo gastronmico
          br
          br
          p El destino se convierte en el eje central de la oferta gastronmica, ya que no solo aporta los productos tradicionales, sino tambin los paisajes, la cultura, tcnicas y platos que definen dicha cultura y que constituyen las experiencias tursticas que se le brindan al visitante.
    p.mt-4 Con esto claro, tambin podemos entender que la gastronoma contribuye a la sostenibilidad de un destino, ya que para transformar un producto agroalimentario en recurso turstico, se debe velar por un correcto mantenimiento del patrimonio gastronmico a travs de la investigacin, la innovacin, el profesionalismo y las buenas prcticas; que posteriormente impacten en la economa y la cultura del lugar, dando a conocer gran cantidad de tradiciones, costumbres y aspectos relacionados con la forma de vivir de los residentes del destino.
    .row.justify-content-center.mt-5
      .col-lg-10
        .cajon.color-primario.p-4.mb-4
          p Hoy da una buena oferta gastronmica no se puede quedar solo en la comida, esta tiene que estar rodeada de una atmsfera, una decoracin y una ambientacin adecuada que aporte originalidad, y que acerque la cocina al turista para que obtenga una conexin no solo con lo local, sino tambin con los cocineros y quienes aportan su profesionalismo al sector. 
          p En Colombia ya se han implementado varias rutas gastronmicas que invitan al turista a recorrer y conocer la comida regional de manera guiada y ordenada; as mismo incentivan a los extranjeros a tener una experiencia distinta y comprender otras maneras de ver el mundo.
    
