      h2 1.1	Tipos de alimentos            
    .row.justify-content-center(data-aos="fade-up")
      .col-lg-6
        img(src='@/assets/curso/tema1/4.png', alt='Texto que describa la imagen')
      .col-lg-6.mt-4
        .row.justify-content-center.color2
          .col-lg-11
            p.p-3 Un alimento es cualquier sustancia de naturaleza compleja que es consumida por los seres vivos para mantener las funciones vitales del cuerpo y reponer lo que se pierde en la actividad fsica del da a da; sus sustancias son las encargadas de ayudar al ser humano en la formacin de algunos tejidos y permitir la locomocin y calor en el cuerpo; adicional a ello, contienen nutrientes y no-nutrientes, como la fibra vegetal que favorece el funcionamiento de la digestin.
        p.mt-2 Casi todos los alimentos son de origen animal o vegetal, aunque existen algunos que no provienen de estas fuentes como los hongos comestibles, incluidos los championes y las bacterias que son usadas en la preparacin de alimentos encurtidos y fermentados como pan con levadura, vino, cerveza, queso, pepinillos y yogur. Por otro lado, no se consideran alimentos las sustancias que no se ingieren o que, una vez ingeridas, alteran las funciones metablicas del organismo como la goma de mascar, el tabaco, los medicamentos y dems drogas.
    .row.justify-content-center.mt-4
      .col-lg-6
        p En cuanto al valor nutritivo de los alimentos, se puede decir que ste depende de la cantidad y calidad de sus componentes y de la presencia o ausencia de sustancias que afecten a su utilizacin nutritiva (digestibilidad, absorcin o metabolismo) o que tengan efectos txicos (Clnica Universidad de Navarra, sf.) 
        p La ausencia o disminucin de nutrientes en el organismo, puede ocasionar enfermedades despus de transcurrido un determinado tiempo, por ello se deben tener presentes los nutrientes esenciales que se deben consumir en una dieta diaria: vitamina A, hierro, calcio, carbohidratos, grasas y ciertos aminocidos.
         

      .col-lg-6
        img(src='@/assets/curso/tema1/5.png', alt='Texto que describa la imagen')
    p.mt-4 Los alimentos se han clasicado de acuerdo a su valor nutritivo en seis grupos bsicos. De estos, dos pertenecen a alimentos de origen animal y cuatro de origen vegetal, y por obvias razones, cada uno ofrece una diferente calidad de protenas, tal como puede apreciar en el siguiente recurso educativo, veamos:
