            p.mt-5 <strong>Comprenden las verduras, las frutas y los cereales y semillas</strong> dentro de las cuales se encuentran, las legumbres (lentejas, guisantes y frijoles) los cereales (trigo, arroz, maz, avena) y las nueces.
      .col-lg-4.mb-3
        .tarjetas-up
