              h4.mb-0 2. Alimentos de origen animal
            
            p.mt-5 Comprenden <strong>toda especie de ganado (ovino, porcino, caprino, bovino),</strong> todas las aves de corral (pavo, pollo, ganso, entre otros), los mariscos y las distintas especies de pescados. Entre los productos derivados de los animales estn comprendidos: leche, queso, mantequilla, huevos, miel, salchichas. 
      .col-lg-4.mb-3
        .tarjetas-up
          img(src='@/assets/curso/tema1/113.png' alt='AvatarTop' style='width:400px')
          .tarjetas-up_card.bg--2-claro.p-4
            .title.d-flex.align-items-center.mb-4
