                i.fas.fa-caret-down.fa-2x.color-primario.me-3
              h4.mb-0 3. Alimentos de origen mineral 
              
            p.mt-5 En este grupo se encuentra la sal de mesa (cloruro de sodio) que le da sabor a las comidas. Las sales minerales mantienen el equilibrio del metabolismo en conjunto con los azcares, evitando la deshidratacin del cuerpo y ayudando a retener agua.    
    p Todos necesitamos una alimentacin lo suficientemente variada que nos garantice un aporte nutritivo adecuado, es por esto que hay otro modo de clasificar los alimentos, que se denomina la rueda de los alimentos, se trata de un recurso grfico proporcionado a la poblacin para seguir una alimentacin segura y equilibrada, basado en la clasificacin de los alimentos en diferentes grupos que tengan caractersticas nutricionales similares. El informe de 2019 del Programa Nacional de Promocin de Alimentos Saludables (PNPAS) incluy una nueva revisin de la rueda de los alimentos para este 2020, podr ver ms en detalle al respecto en el siguiente recurso:
    .row.justify-content-center.mt-5
