                p Este grupo contiene gran cantidad de hidratos de carbono, de tal forma que son los utilizados para el suministro de caloras en la dieta. Tambin poseen cantidades de algunos minerales y vitaminas del complejo B. La digestibilidad es variable en funcin del contenido en materia no degradable.
