      h2 1.2	Propiedades organolpticas de los alimentos
    p Los alimentos son una fuente imperiosa de estmulos relacionados directamente con la calidad. Es por eso que los expertos analizan esta calidad a partir de los sentidos. Cuando disfrutamos de una buena comida los cinco sentidos estn activos; no solo el gusto est involucrado en la accin de comer, sino tambin todos los otros sentidos y por ello se habla de propiedades organolpticas, que son las caractersticas fsicas que son percibidas por los distintos sentidos, como el sabor, el olor, la textura y el color.
    p Los expertos en restaurantes o los especialistas en nutricin los utilizan para comprobar la calidad de los alimentos o para conocer los gustos de los consumidores o para innovar en las recetas. Su funcin es la siguiente:
    .row.justify-content-center.mt-5(data-aos="fade-up")
      .col-lg-6.bg2
        h4.text-white.text-center.mt-3 Propiedades organolpticas de los alimentos
    .row.justify-content-center.mt-5(data-aos="fade-up")
      .col-sm-6.col-xl-6.mb-4.mb-xl-0
        .tarjeta.tarjeta-flip.color-primario(@mouseover="indicadorTarjetaFlip = false")
          .indicador--hover(v-if="indicadorTarjetaFlip")
          .tarjeta-flip__contenedor
            .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema1/133.png')})`}")
            .tarjeta-flip__contenido.p-4.p-xl-5
              img.wii(src='@/assets/curso/tema1/ico1.svg', alt='Texto que describa la imagen')
              p Es el sentido ms obvio cuando se degusta un plato y es posible gracias a las papilas gustativas de la lengua que son capaces de identificar cinco tipos de sabores: dulce, salado, amargo, cido y umami (sabroso) Dependiendo del rea de la lengua, se percibirn ms unos sabores que otros.
 

      .col-sm-6.col-xl-6.mb-4.mb-xl-0
        .tarjeta.tarjeta-flip.color-secundario(@mouseover="indicadorTarjetaFlip = false")
          .tarjeta-flip__contenedor
            .tarjeta-flip__img.p-4(:style="{'background-image': `url(${require('@/assets/curso/tema1/fon.png')})`}")
              img.wii(src='@/assets/curso/tema1/ico3.svg', alt='Texto que describa la imagen')
              p Es un indicador de la calidad del alimento, del tiempo de su coccin, su frescura o deterioro. Nos permite observar reacciones qumicas que se producen en los alimentos tras someterlos a algn proceso trmico, como por ejemplo la carne; es un alimento que puede variar en su color dependiendo del trmino en el que est cocinada; as podr ser en ocasiones ms oscura o ms clara, dependiendo del gusto del comensal. Muchas de las variaciones de color son normales y no afectan la inocuidad. 

            .tarjeta-flip__contenido.t1
              img(src='@/assets/curso/tema1/15.png', alt='Texto que describa la imagen')

      .col-sm-6.col-xl-6.mb-4.mb-xl-0.mt-4
        .tarjeta.tarjeta-flip.color-secundario(@mouseover="indicadorTarjetaFlip = false")
