            .tarjeta-flip__contenido.p-4.p-xl-4
              img.wii(src='@/assets/curso/tema1/ico2.svg', alt='Texto que describa la imagen')
              p Lo percibimos gracias al sentido del olfato, pero es considerada una de las sensaciones ms difciles de definir y caracterizar, ya que se da debido a las molculas voltiles que desprenden los alimentos, bien sea de manera natural o a partir de su procesado (por medio de aditivos alimentarios, como los aromas artificiales). 
              p Los seres humanos podemos distinguir hasta diez mil olores diferentes aunque es muy difcil catalogar el olor, lo que normalmente sucede, es que creamos analogas para describirlo como, por ejemplo: aromas que se asemejan a la madera, las flores o algo que se est ahumando. 
