      h2 1.3	Parmetros de inocuidad alimentaria
    p La inocuidad de los alimentos puede definirse como el conjunto de condiciones y medidas necesarias durante la produccin, almacenamiento, distribucin y preparacin de alimentos para asegurar que una vez ingeridos, no representen un riesgo para la salud (Ministerio de Salud Colombiano, sf.)
    .row.justify-content-center
      .col-lg-6
        img(src='@/assets/curso/tema1/177.png', alt='Texto que describa la imagen')
      .col-lg-6.mt-3
        p Este aspecto es fundamental en la salud pblica y es esencial para la gestin de la calidad total, por lo cual es tema de alta prioridad para todos los gobiernos, consumidores y productores. En cuanto a los restaurantes como tal, este tema es primordial: son ellos los llamados a asegurar las condiciones necesarias para brindar preparaciones en ptimo estado, que cuenten con una buena higiene y que garanticen la mxima seguridad. En concordancia, los establecimientos gastronmicos deben evitar intoxicaciones por bacterias (como Salmonella, transmitidas a travs de comida contaminada) tanto en empleados como en clientes, y que generan el desprestigio del establecimiento, la imposicin posterior de multas o en su defecto, cierre obligatorio por parte de las entidades de inspeccin sanitaria.
        .row
          .col-lg-12
            .cajon.color-acento-botones.p-1.mb-4
              p.p-4 Dichas entidades son las encargadas de visitar a los restaurantes para inspeccionar si cumplen o no, con los requisitos legales para la venta y distribucin de alimentos. Se puede incluir la toma de muestras para el anlisis cientfico y los registros de inspeccin. 
    .row.justify-content-center.mt-4
      .col-lg-10.col2.p-4
        .row
