            p.text-small.mb-0 Para minimizar los riesgos en los establecimientos gastronmicos colombianos, el gobierno y puntualmente la Subdireccin de Salud Nutricional, Alimentos y Bebidas, han creado una gua orientadora de la normatividad sanitaria vigente, dirigida a quienes ejercen actividades de manipulacin de alimentos y bebidas en establecimientos gastronmicos; la puede consultar en el siguiente enlace: 
          .col-lg-2
            .mt-3
              a.boton.me-3.indicador__container(
                :href="('https://www.minsalud.gov.co/sites/rid/Lists/BibliotecaDigital/RIDE/VS/PP/SNA/Guia-inocuidad-alimentos-establecimientos-gastronomicos-restaurantes.pdf ')"
                target="_blank"
