                @mouseover="mostrarIndicador = false"
              )
                span Descargar
                i.fas.fa-link
                .indicador--click(v-if="mostrarIndicador")
    p.mt-5 En cuanto a la higiene de los alimentos ms comunes en los restaurantes, debe regularse por el conjunto de normas, pautas y procedimientos que cada organizacin debe seguir, asegurando de esta forma la inocuidad e idoneidad de los alimentos. El objetivo es preservar la salud del consumidor respetando los principios que se explican a continuacin en cada una de las etapas de la cadena alimenticia.
