      .tarjeta.color-primario
        .row.justify-content-center.mb-3
          .col-12
            img(src='@/assets/curso/tema1/222.png' alt='AvatarTop')
        .row.p-3
          h4.mb-1 Carne
          p.mb-5 Para pollos y carnes rojas los jugos deben ser de un color claro.
    p En cuanto a los factores de riesgo habituales que pueden afectar los alimentos, se encuentran principalmente:
    .row.mt-5
    .row.justify-content-center
      .col-md-6.mb-5.mb-md-0
        //- LineaTiempoD debe ir acompaado de una de una de estas clases => 
        //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones
        LineaTiempoD.color-acento-botones
          p.text-small(numero="1" titulo="Mantenimiento de alimentos a temperaturas inadecuadas") Este riesgo suele aparecer cuando se rompe la cadena del fro, que provoca que los alimentos no se conserven adecuadamente. Esto ocurre cuando por ejemplo pasa un cierto lapso de tiempo entre la entrega de productos por parte de los proveedores y el almacenamiento por parte de los empleados del restaurante.
          
          p.text-small(numero="2" titulo="Coccin inadecuada") Un huevo frito poco hecho, una hamburguesa medio cruda o unos calamares poco pasados pueden ser un riesgo, ya que generan una mayor proliferacin de microorganismos patgenos que generan las intoxicaciones en el ser humano. Por ello, se debe insistir en la profesionalizacin de los cocineros y en sus certificados de manipulacin de alimentos al interior de cualquier restaurante.
          
          p.text-small(numero="3" titulo="Contaminacin en utensilios e instalaciones") Es necesario hacer uso de productos desinfectantes para reducir el riesgo de posibles microorganismos.
          
          p.text-small(numero="4" titulo="Proveedores de alimentos poco fiables") Para cada restaurante debe ser un hbito revisar y controlar la entrega de productos por parte de proveedores. Se debe tener en cuenta la temperatura, el etiquetado, los envases y embalajes, los medios en los que se transportan los productos, etc.; adicional a ello, se deben pedir los correspondientes certificados de calidad a quienes proveen los alimentos al establecimiento. 
          
          p.text-small(numero="5" titulo="Mala gestin de residuos") Eso incluye mantener residuos en reas de trabajo o botes sin la debida separacin de los alimentos.
          
          p.text-small(numero="6" titulo="Malas prcticas de almacenamiento de productos") Garantizar la salubridad de los mismos. Para ello, es esencial seguir unas estrictas pautas de almacenamiento y evitar malas prcticas como, por ejemplo, colocar los productos en el suelo aunque estn protegidos por cajas.
          p.text-small(numero="7" titulo="Mala higiene personal de los manipuladores de alimentos") Se debe procurar que todos los empleados del restaurante tengan claro los habitos bsicos en cuanto a su aseo personal, indumentaria e higiene en general.
      
      .col-lg-6
        img(src='@/assets/curso/tema1/233.png' alt='AvatarTop')
