    #t_1_4.titulo-segundo.color-acento-contenido
      h2 1.4	Caracterizacin de mens 
    .row.justify-content-center
      .col-lg-6
        img(src='@/assets/curso/tema1/232.png' alt='AvatarTop')
      .col-lg-6.mt-3
        .row.justify-content-center.color2
          .col-lg-12
            p.p-3 La palabra men naci en los primeros restaurantes de Francia y tiene su raz en el vocablo latino minutus, que significa pequeo. El trmino se comienza a usar en 1718 y muchos autores lo han definido en resumen como una presentacin de las comidas, bebidas y postres disponibles para el comensal. Es el conjunto cerrado de platos que un establecimiento ofrece al cliente.
        p.mt-4 Este trmino en ocasiones se confunde con la palabra carta, sin embargo, existe una diferencia puntual y es que sta ltima est hecha en cartn, papel o cualquier otro medio y describe detalladamente los precios de los platos y bebidas; es decir, presenta el conjunto de todos los platos que ofrece el restaurante y en el que suele estar incluido el men del da.
    .row.justify-content-center.mt-4
      .col-lg-6
        p Otra diferencia es que de una carta se pueden elegir varias combinaciones de platos; pero cuando se habla de men, se est haciendo referencia a una propuesta de comida diseada por el chef cuya composicin puede variar dependiendo de su gusto, de la ocasin o del tipo de restaurante. 
        .row.justify-content-center.color3
          .col-lg-12
            p.p-3 Adicional a lo anterior, el precio tambin es un diferencial ya que el del men es un valor cerrado, mientras que la eleccin de varias combinaciones desde la carta, tiene un valor variable. 
            p.p-3 A estos dos trminos se le puede agregar el tercero y es la comanda; que es la eleccin de platos que se ha hecho despus de consultar la carta y que es llevada hasta la cocina, directamente por el mesero quien atendi la mesa.

      .col-lg-6
        img(src='@/assets/curso/tema1/24.png' alt='AvatarTop')
    .row.justify-content-center.mt-5
      .col-lg-10
        .cajon.color-primario.p-4.mb-4
          p Elaborar el men parece fcil, pero en realidad es uno de los aspectos ms difciles de hacer dentro de una cocina, ya que debe buscar el equilibrio entre varios factores como lo son los productos disponibles, la especialidad del restaurante, el tipo de comensal al cual va enfocado, los precios de los alimentos, margen de utilidad, entre otros.  El objetivo principal siempre ser crear platos rentables para el restaurante y que estos sean los ms pedidos por los comensales, por ello es importante analizar la composicin del men teniendo en cuenta las variables mencionadas.
    p.mt-4 Una vez terminado el diseo del men y de la carta, ser importante darlas a conocer de manera digital, haciendo uso de redes sociales o de la pgina <i>web</i> del restaurante; esto con la intencin de impulsar las ventas y dar a conocer la marca a ms clientes.
    p En cuanto a los principales tipos de mens, son incontables, pues dependen del tipo de oferta de cada establecimiento, adems de la manera en que se encuentren agrupados los platos ofertados, sin embargo, a continuacin, podr tener una clasificacin general de estos mens:
    .tarjeta.tarjeta--azul.p-4.bg3
      SlyderA(tipo="b")
        .row.p-4
          .col-md-5.mb-4.mb-md-0.mt-5
            .row.mt-5
            .row-mt-5
            <br>
            h5.mt-5 Men a la carta
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Los precios tienden a ser ms altos.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Ofrecen mucha ms flexibilidad ya que los comensales pueden elegir productos individuales y combinarlos como quieran.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Brindan una mejor experiencia gastronmica a los clientes.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Contienen la seccin de la comida: ensaladas, pastas, carnes, pescados, etc.; y de las bebidas como refrescos, cervezas, vinos y licores
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Los mens a la carta son especialmente fciles de leer.
          .col-md-6
        .row.p-4
          .col-md-5.mb-4.mb-md-0.mt-5
            .row.mt-5
            .row-mt-5
            <br>
            h5.mt-5 Men del da o men ejecutivo
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Cambian a diario, segn los productos que estn disponibles o lo que haya preparado el cocinero.

              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Este men por lo general, incluye en el precio una entrada, un plato principal, un postre, y una bebida.

              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Los mens del da pueden parecer rgidos, pero son una opcin excelente para que los cocineros tengan mayor flexibilidad a la hora de elegir los alimentos que desean servir en un restaurante.
          .col-md-6
        .row.p-4
          .col-md-5.mb-4.mb-md-0.mt-5
            .row.mt-5
            .row-mt-5
            <br>
            h5.mt-5 Men cclico
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Son mens preparados para abarcar determinado periodo de tiempo: una semana, un mes, 3 meses, etc.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Por ejemplo, podra haber un men del da diferente durante dos semanas, y luego el ciclo del men comenzara nuevamente desde el principio.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Al final de cada perodo pueden ser usados de nuevo los mens, evitando as la necesidad de crear unos nuevos.
          
          .col-md-6
        .row.p-4
          .col-md-5.mb-4.mb-md-0.mt-5
            .row.mt-5
            .row-mt-5
            <br>
            h5.mt-5 Men degustacin
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Est compuesto por una gran cantidad de platos elegidos por el restaurante y generalmente considerados especialidades de la casa.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Se utiliza para dar a los comensales la oportunidad de que puedan probar una gran variedad de platos elegidos por el chef.

              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Algunos de ellos suelen llevar algunas tcnicas de gastronoma molecular, como la esferificacin, espuma, deconstruccin, etc.

          .col-md-6
        .row.p-4
          .col-md-5.mb-4.mb-md-0.mt-5
            .row.mt-5
            .row-mt-5
            <br>
            h5.mt-5 Men por tiempos
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Es un men que el cliente ha contratado con antelacin y es concertado con el chef del restaurante, tanto en el precio como en la composicin de los platos que lo integran.

              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Este tipo de men es el ms usado para grandes grupos en <i>catering</i> y eventos.
        .row.p-4
          .col-md-5.mb-4.mb-md-0.mt-5
            .row.mt-5
            .row-mt-5
            <br>
            h5.mt-5 Men por tiempos
            ul.lista-ul
              li 
                p.text-small.mb-0 Se refiere a que cada uno de los tiempos es un diferente platillo por servir. Existen mens de tres y cuatro tiempos
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Men de tres tiempos: entrada, plato fuerte y postre.
              li 
                i.fas.fa-angle-right.ss
                p.text-small.mb-0 Men de cuatro tiempos: pasabocas, entrada, plato fuerte y postre.
    .row.justify-content-center.mt-5
      .col-lg-4
        .row.justify-content-center.color2
          .col-lg-12
            p.p-3 Como ya se haba indicado tambin existe la carta de platos, cuyo objetivo es promocionar y dar a conocer los productos que se ofrecen en un restaurante, por ello es fundamental su diseo, para obtener la atencin de los comensales.
            p.p-3.mb-0 Es necesario pensar en diferentes tems cuando se est a punto de disear la carta: en caso de ser impresa, se deber considerar la resistencia, la firmeza, la textura, el color y la tipografa para describir los platos; esto con el fin de generarle una fcil lectura al cliente.

      .col-lg-4.mt-2
        img(src='@/assets/curso/tema1/25.png' alt='AvatarTop')
      .col-lg-4
        .row.justify-content-center.color4
          .col-lg-12
            p.p-3 En caso de ser una carta digital, se tendrn en cuenta los mismos factores, omitiendo obviamente lo concerniente al tipo de papel.
            p.p-3.mb-5 Se debe tener especial cuidado con el uso del color, ya que, segn el neuromarketing, este puede estimular el apetito del cliente y reflejar la imagen del restaurante. A continuacin, se explican los diferentes tipos de cartas que hay, as como las consideraciones en su diseo, veamos:
    .tarjeta.tarjeta--azul.p-4.mb-5.mt-5.bg4
      SlyderA(tipo="b")
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Carta central
            p.mt-5 Este formato sirve para mens cortos, de servicio rpido y donde los clientes estn familiarizados con las opciones que van a encontrar en el men. Es comn encontrarlo en restaurantes de comidas rpidas ya que permite que el comensal lea el men mientras hace la fila. 
        
          .col-md-6  
              img(src='@/assets/curso/tema1/80.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Formato de una sola pgina
            p.mt-5 Se utiliza cuando se tienen pocos productos para ofrecer y no requieren de mucha explicacin. Es muy comn encontrarlos en restaurantes de comida casual y <i>gourmet</i>. Funcionan en diferentes tipos y formas, a una sola cara o por ambas. La ventaja de este estilo de men es que es muy sencillo y flexible, adems de que es de bajo costo. 

        
          .col-md-6   
              img(src='@/assets/curso/tema1/26.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Formato de mltiples pginas
            p.mt-5 Este men es utilizado cuando existe una larga lista de platos que requieren de una descripcin. Tambin, si en su oferta tiene entradas, postres, platos fuertes y bebidas. El problema que tiene este tipo de men es que el comensal se demora ms tiempo en decidir, revisar y pedir. Lo que trae como consecuencia que se retrase la facturacin y se estanque la rotacin.
          .col-md-6
              img(src='@/assets/curso/tema1/27.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Secciones de la carta
            p.mt-5 Para crear y disear una carta de restaurante, es importante que la dividas por grupos:
          .col-md-6.azxc
            .row.pt1.p-2
              p <strong>Primero</strong> pueden incluir aquellos platos que estimulan el apetito del cliente Para picar: entradas, sopas, cremas, aperitivos
            .row.pt2.p-2
              p En un <strong>segundo grupo</strong> se colocan los platos ligeros: ensaladas, sndwiches, tortillas, arroces y pastas.
            .row.pt1.p-2
              p En una <strong>tercera parte</strong> coloca los platos principales y fuertes: carnes blancas, rojas, pescados o platos de la casa.
            .row.pt2.p-2
              p Por ltimo, coloca los postres (tanto fros como calientes), y las principales bebidas.
              p Si se tiene una seleccin grande de vinos, <i>cocktails</i> o alcoholes fuertes, incluye otra carta especial para este tipo de bebidas.
   
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Aspectos que se deben incluir para disear la carta

            p.mt-5 <strong>1. Datos del negocio:</strong> son elementos que ayudarn al cliente a identificar el negocio: nombre, logotipo de manera visible, direccin, telfono de contacto, correo electrnico y, muy importante hoy en da las redes sociales!
            p <strong>2. Define tu pblico objetivo:</strong>  esto es esencial para saber cul ser el mejor estilo de carta y tambin ayudar a elegir los colores, los grficos, los platos destacados, etc.
            p <strong>3. La propuesta gastronmica:</strong>   se debe tener por escrito los platillos que se quieren ofrecer, una breve resea que describa su preparacin y el precio. <strong>Comienza con los ttulos o categoras y luego detalla las opciones.</strong> 
          .col-md-6 
              img(src='@/assets/curso/tema1/29.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Categoriza y simplifica la informacin


            p.mb-0 Debes categorizar el orden en el que va a presentar la oferta gastronmica. Los comensales estn acostumbrados a leer primero: entradas, primer plato, segundo plato.  Si la oferta de postres y bebidas es grande, se recomienda tener una carta aparte.

            p.mb-0 <strong>Establece los precios de la carta</strong>
            p.mb-0 Se pueden seguir dos estrategias. 
            p.mb-0 Hacer que el cliente no piense en el gasto y no se guie por los precios: no se incluye el smbolo de $ o se coloca el precio con la letra K a lo ltimo (que indica miles). Ejemplo 10 K.
            p Otra opcin es exponer el valor de lo que se ofrece, es decir, mostrar por qu los platos ms caros de la carta cuestan ms. Por ejemplo, infrmale al cliente si el plato incluye una carne de primera calidad, verduras orgnicas o un proceso artesanal que consume ms tiempo.

          .col-md-6
              img(src='@/assets/curso/tema1/300.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Define la lnea de diseo
            p esto va depender del pblico objetivo y la propuesta de tu restaurante.
            p.mb-0 Ac se deben tener en cuenta:
            p.mb-0 <strong>- Los colores:</strong> Establecer un fondo neutro, preferiblemente blanco, es recomendable para lograr que la comida luzca bien y se destaque; tambin cuida la iluminacin y el formato de las imgenes. El rojo, naranja, amarillo son tonos secundarios ideales para abrir el apetito. Si se desea una carta elegante, se puede optar por los clsicos negro y blanco.
            p.mb-0 - <strong>El tipo de papel</strong>, tipo de acabado o la plataforma digital en la que se har
            p.mb-0 - <strong>El tipo de letra:</strong> tamao equilibrado, que sea legible (y ayudar a la percepcin del cliente), con letras de color oscuro sobre un fondo claro, y facilitando la lectura.
            p.mb-0 - <strong>El tipo de carta a disear</strong> (segn lo que ya se vio anteriormente).
            p Tono de la redaccin: segn el tipo de cliente que se tenga, sus edades, su estilo. En un establecimiento de jvenes puedes redactar la carta en un lenguaje informal o jovial, destacar las redes sociales o los elementes interactivos del men.
          .col-md-6
              img(src='@/assets/curso/tema1/311.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h5 Y por ltimo, pero no menos importante, esmrate con las fotos

            p.mt-5 No es imprescindible que en una carta de restaurante haya fotos de los platos, pero si se incluyen, deben ser de buena calidad.

          .col-md-6 
              img(src='@/assets/curso/tema1/32.png', alt='Texto que describa la imagen')
    separador            
    #t_1_5.titulo-segundo.color-acento-contenido
      h2 1.5	 Caracterizacin de bebidas 
    .row.justify-content-center
      .col-lg-6
        img(src='@/assets/curso/tema1/33.png', alt='Texto que describa la imagen')
      .col-lg-6
        .row
          .col-lg-12
            .cajon.color-primario.p-4.mb-4.mt-2
              p La palabra bebida es de uso comn, y se refiere a todo tipo de lquidos (naturales o artificiales) que puedan ser utilizados para el consumo humano y cuyo principal objeto es calmar la sed o hidratar nuestro organismo. 
              p El concepto de bebida se relaciona puntualmente con una de las necesidades primarias del ser humano que es reponer los lquidos que utiliza en la realizacin de sus actividades diarias. 
        
        p La bebida por excelencia es el agua ya que es la ms recomendada para cumplir dicha funcin de reposicin de lquidos; sin embargo, el ser humano tambin ha creado otras bebidas ms complejas que son frecuentemente usadas en celebraciones. Como lo son las bebidas alcohlicas y las gaseosas, cuyo objetivo primordial es sumarle placer, gusto y elementos visuales a la experiencia de beber. Adicional a estas, tambin existen las infusiones o jugos de frutas que pueden ser o no, artificiales o contener o no, azcares aadidos. 
    p Hoy en da, se puede encontrar en el mercado una gran variedad de bebidas con un presupuesto y un tipo de consumidor especfico al que van dirigidas; algunas de ellas estarn solo enfocadas en adultos, mientras que otras sern de consumo apto para todos.
    TabsB.color-primario.mb-5
      .py-4.py-md-5(titulo="Bebidas alcohlicas" :icono="require('@/assets/curso/tema1/ico5.svg')")
        .row
          .col-md-7.mb-4.mb-md-0
            p Se considera una bebida alcohlica si contiene ms de 2.5 grados de alcohol etlico.
            .row
              .col-lg-6.color22
                p.mb-0.p-2 Existen dos tipos de bebidas alcohlicas: 
                ul.lista-ul.p-2.mb-0
                  li.mb-0
                    i.fas.fa-angle-right.ss
                    p.mb-0 Las fermentadas.
                  li 
                    i.fas.fa-angle-right.ss
                    p.mb-0 Las destiladas.
            p.mb-0.mt-4 Las bebidas fermentadas son las procedentes de frutas o de cereales que, por accin de ciertas sustancias microscpicas (levaduras), convierten el azcar en alcohol.
            p Las bebidas fermentadas ms comunes son el vino, la cerveza y la sidra.
            p.mb-0 Las bebidas destiladas son el resultado del proceso de separacin de agua y alcohol de un lquido previamente fermentado. El mtodo de destilacin puede ser industrial o artesanal, dependiendo del volumen de produccin y de la calidad deseada para el producto final. En cualquier caso, su objetivo es el de obtener una bebida de alcohol puro con un nivel superior a los 40.
            p Entre las bebidas destiladas ms conocidas se encuentran: el <i>coac</i> o <i>brandy</i>, la ginebra, el <i>whisky</i>, el ron y el <i>vodka</i>.

          .col-md-5
              img(src='@/assets/curso/tema1/i1.png', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Bebidas no alcohlicas" :icono="require('@/assets/curso/tema1/ico6.svg')")
        .row
          .col-md-7.mb-4.mb-md-0
            p Son las que en su composicin entra el agua potable gasificada o no, adicionada con cualquiera de las sustancias siguientes: azcares, jugos de frutas, extractos vegetales permitidos, cidos orgnicos (ctrico, lctico, fumrico, glucnico, mlico y tartrico), esencias y colorantes naturales o sintticos autorizados.
            p Entre ellas se encuentran:
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Las gaseosas

              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Los jugos
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Las bebidas energizantes
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 El agua
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Las aguas saborizadas
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Bebidas deportivas
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 El t
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Las infusiones
          .col-md-5
              img(src='@/assets/curso/tema1/i2.png', alt='Texto que describa la imagen')
    p.mb-0 Al igual que con el men de alimentos, las bebidas tambin exigen unas consideraciones en el diseo de su carta.
    p Si hablamos del mercado de bebidas debemos saber que tiene un gran margen de beneficio, llegando a triplicar su coste con respecto al precio de venta al pblico (PVP). Adems, las bebidas son un producto que se puede vender por unidades y de rpido servicio.
  
    .row.mt-5
      .col-lg-5.bg5
        h4.text-white.text-center.mt-3 Consideraciones en la carta de bebidas    
    .row.justify.content-center.mt-4
      .col-lg-6
        LineaTiempoD.color-acento-botones
          p(numero="1" titulo="Estructura clara") Una estructura sencilla facilitar la eleccin de los clientes, por ejemplo: <br>Bebidas calientes o fras.<br>Bebidas clsicas o especiales.<br>Bebidas pequeas o grandes.
          p(numero="2" titulo="Mezcla los precios") Los precios de la carta de bebidas tienen que estar mezclados, es decir, no coloques las bebidas ms caras por un lado y las menos costosas por otro. <br>Mezclar los precios har que tu cliente consuma ms de lo que tena pensado.          
          p(numero="3" titulo="Texto") El objetivo es llamar la atencin del cliente, un exceso de texto en la carta har que no le preste tanta atencin al producto. El texto de la carta tiene que ser breve, explicativo y claro. Se pueden usar palabras atractivas como natural, fresco o local
          p(numero="4" titulo="Separa las cartas de bebida") Un error clsico es juntar los vinos con el resto de bebidas, debe crear dos cartas, una con los vinos de comercializacin, y otra con el resto de las bebidas. 
          p(numero="5" titulo="Destaque sugerencias o especialidades de la casa") Ponga algunas bebidas en negrita, maysculas o cursiva, relcelas con imgenes atractivas o colores sugerentes. Pero no exagere demasiado o el efecto se esfumar.
          p(numero="6" titulo="El diseo") Una vez definida la estructura hay que decidir el diseo. La carta es un buen escaparate, se deben tener en cuenta varios factores para su elaboracin, como el estilo y el diseo de la portada, el tipo de letra, el color e imgenes, el tipo de papel y la distribucin del contenido.
      .col-lg-6.mt-2  
        img(src='@/assets/curso/tema1/i3.png', alt='Texto que describa la imagen')
    p.mt-5 La tendencia actual es tener cartas reducidas porque facilitan al cliente su eleccin, acortan el tiempo de toma de la comanda y reducen el nmero de artculos de almacn.
    .row.justify-content-center.mt-4
      .col-lg-10.col2.p-4
        .row
          .col-lg-1
            img.tamimg(src='@/assets/curso/tema1/ico.svg', alt='Texto que describa la imagen')
          .col-lg-9
            h5.mt-4 Puede consultar una carta de bebidas a manera de ejemplo, en el siguiente enlace:

          .col-lg-2
