                @mouseover="mostrarIndicador = false"
              )
                span Ingresar
                i.fas.fa-link
                .indicador--click(v-if="mostrarIndicador")
    
    p.mt-4 En relacin a la coleccin de bebidas nacionales, son varias y variadas las que degustan el paladar de propios y forneos, podr consultar las ms conocidas en el siguiente recurso educativo: 
    .tarjeta.tarjeta--gris.p-4
      //- PasosB debe ir acompaado de una de una de estas clases => 
      //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones
      PasosB.color-primario
        .row.justify-content-center(titulo="Agua de panela")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Agua de panela
            p El agua de panela, o aguapanela, como se le conoce comnmente, es una de las bebidas tradicionales, es refrescante, energizante y al mismo tiempo, un remedio casero para aliviar los das de resfriado. Se prepara con panela, agua y en algunas ocasiones, con zumo de limn, y se consume en todas las regiones de Colombia, ya sea caliente o fra.
            p La aguapanela es especialmente popular en la cultura colombiana, pues hace parte del da a da de los colombianos. Se suele acompaar con queso, almojbana o arepa. Incluso, tiene una versin con alcohol conocida como canelazo una preparacin a base de canela y aguardiente.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/50.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Champs colombiano")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Champs colombiano
            p Esta bebida, mayormente conocida y consumida en el Valle del Cauca, se prepara con melado de panela, maz y frutas locales como: lulo, pia o guanbana, clavos de olor y canela al gusto. Una preparacin que se lica y se sirve con mucho hielo como refresco para los intensos das de calor en la capital de la salsa. Se suele acompaar del tradicional pandebono o de empanadas vallunas.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/51.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="La chicha")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 La chicha
            p Es una de las bebidas tradicionales latinoamericanas, a base de maz fermentado con azcar. En Colombia, dependiendo de la regin del pas se prepara a base de pia o yuca, aunque es propia de la regin andina colombiana. Su grado de alcohol, as como su sabor dulce o amargo vara dependiendo del tiempo de fermentacin.
            p La chicha es originariamente una bebida indgena Muisca, que poco a poco se volvi del comn, tanto as que hoy en da se puede conseguir en el centro de Bogot.

          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/52.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Refajo")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Refajo
            p Nada ms colombiano que un refajo, una bebida que resulta al combinar cerveza con gaseosa, generalmente de sabor kola roja o con la famosa Colombiana. Esta es una de las bebidas ms apetecidas en la mesa de los colombianos, ya sea para celebrar o simplemente para compartir una comida, pues desde los aos 90s se convirti en una costumbre, tanto as que lleg al mercado con una presentacin propia.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/53.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Masato")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Masato
            p Es una de las bebidas artesanales ms populares en los departamentos de Cundinamarca, Santander y Tolima. Al igual que la chicha, es fermentada y elaborada a base de cereales como el arroz, el maz y el trigo, o tubrculos como la yuca. Se suele acompaar de una almojbana o pan de yuca, se consume fra y como merienda (en la maana o en la tarde).

          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/54.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Viche")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Viche
            p Esta bebida artesanal tpica del Pacfico colombiano se fabrica a partir del jugo de la caa de azcar y se le agregan diferentes ingredientes para darle un sabor ms agradable. Su contenido de alcohol es similar al del aguardiente, incluso del viche se derivan otras bebidas colombianas como el arrechn, el tumbacatre y el tomaseca. En el pasado, el viche fue utilizado para dolores estomacales, as como para aumentar el vigor y la fertilidad, y siempre ha sido conocido por sus efectos afrodisacos.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/i4.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Aguardiente")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Aguardiente
            p Es coloquialmente conocido como guaro y representa una de las bebidas colombianas por excelencia. El aguardiente es una bebida destilada a base de caa de azcar y ans, considerada por los colombianos como el licor nacional. Tradicionalmente se disfruta solo y fro.
            p Su consumo es muy popular en las diferentes ciudades de Colombia, en donde se comercializan marcas emblemticas como el Tapa Roja del Tolima, el Doble Ans del Huila, el Antioqueo, el Nctar de Cundinamarca, entre otros. Incluso, hoy en da han salido al mercado marcas colombianas premium o ultra premium como el Aguardiente Mil Demonios.

          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/i5.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Chirrinchi")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Chirrinchi
            p Es una de las bebidas tradicionales de los pueblos originarios Wayuu en La Guajira colombiana. Es un licor que se obtiene al destilar la panela de caa de azcar, mezclado con varias especias. Para los indgenas Wayuu es una bebida muy importante como parte de sus celebraciones y rituales. Sin embargo, en Bogot se puede probar un chirrinchi distinto, a base de hierbas y frutos como el hinojo, la limonaria y la manzanilla.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/i6.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Boroj")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Boroj
            p A esta bebida se le atribuyen efectos afrodisacos, al mismo tiempo, propiedades nutricionales y sanadoras. 
            p Se trata de un jugo preparado con boroj, una fruta de color morado o marrn (que solo se da en el Choc), agua, leche, azcar o panela, vainilla, huevos y hielo. Usualmente se toma al desayuno o para acompaar una merienda.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/i7.png', alt='Texto que describa la imagen')
        .row.justify-content-center(titulo="Sabajn")
          .col-md-5.mb-4.mb-md-0.mt-5
            h5 Sabajn
            p Es un licor a base de aguardiente, muy popular por su sabor dulce y cremoso, debido a que incluye: leche, yema de huevo, leche condensada y canela. 

            p Esta es una de las bebidas tradicionales de las Navidades colombianas, pero tambin se disfruta durante todo el ao. Generalmente se sirve fro, como aperitivo.
          .col-md-6.mt-5
            figure
              img(src='@/assets/curso/tema1/56.png', alt='Texto que describa la imagen')
    separador
    #t_1_6.titulo-segundo.color-acento-contenido
      h2 1.6	 Emplatados y presentacin de platos
    .row.justify-content-center
      .col-lg-4
        .row.justify-content-center
          .col-lg-12
            .cajon.color-acento-contenido.p-4
              p.mb-0 Desde tiempos de los romanos, se comenzaron a presentar platos originales que tuvieran coherencia con el gusto por los excesos y el lujo que marcaba dicha poca. Se valoraba entonces el mrito de un cocinero por su capacidad de innovar e impresionar a los comensales.
        p.mt-4 En otras culturas, por el contrario, simplemente se prestaba atencin a la distribucin de los trozos de comida en el plato para que al comensal le fuera ms sencillo el acto de comer; con el tiempo, la evolucin de la cocina introdujo aspectos artsticos, donde los alimentos se disponen de diferentes maneras y se tiene en cuenta el color y las formas para generar emociones y sorprender al comensal.        
      .col-lg-4
        img.tamimg(src='@/assets/curso/tema1/i8.png', alt='Texto que describa la imagen')

      .col-lg-4.mt-2
        p.mb-0 En la actualidad, el emplatado sigue tomando un rol importante a la hora de generar un efecto agradable a la vista de quien degustar la preparacin. 
        p Lo que se ve, es el primer estmulo que participa en el juego de las sensaciones a la hora de evaluar o querer consumir un plato, por esta razn la armona y la esttica deben tener un peso importante; posteriormente aparece el olfato y por ltimo el gusto, por tanto, si queremos que la receta cautive, se debe prestar atencin a la presentacin que ser lo primero que se juzgue.
        p Justamente para lograr dicho objetivo, se habla de las tcnicas de emplatado que son parte importante de la cocina contempornea donde el atractivo visual es clave para el xito de un plato. 
    .row.justify-content-center.mt-5
      .col-lg-10
        .row
          .col-lg-12
            .cajon.color-primario.p-4.mb-4
              h5 Emplatar
              p Es colocar los elementos de una preparacin en el plato, de modo que al comensal le resulte agradable a la vista y le resulte fcil y placentero de comer.
    p Al momento de emplatar, es necesario tener en cuenta cuales son los componentes del plato a servir, algunas de las consideraciones ms comunes para llevar a cabo esta tarea, se encuentran descritas a continuacin:        
    SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
      .tarjeta.color-acento-contenido.color12
        .row.justify-content-center
          .col-12
            img(src='@/assets/curso/tema1/82.png' alt='AvatarTop')
        .row.p-3
          h4.mb-1 La protena
          p.mb-5 En general es la protagonista del plato.
          .row.mt-5

      .tarjeta.color-acento-contenido.color12
        .row.justify-content-center
          .col-12
            img(src='@/assets/curso/tema1/58.png' alt='AvatarTop')
        .row.p-3
          h4.mb-1 La guarnicin de carbohidratos
          p.mb-5 Constituye el acompaamiento primario del plato, est constituida por el arroz, las patatas, la pasta, etc.

      .tarjeta.color-acento-contenido.color12
        .row.justify-content-center
          .col-12
            img(src='@/assets/curso/tema1/60.png' alt='AvatarTop')
        .row.p-3
          h4.mb-1 La guarnicin vegetal
          p.mb-5 Son las verduras que dan vitaminas y color.
          .row.mt-4
      .tarjeta.color-acento-contenido.color12
        .row.justify-content-center
          .col-12
            img(src='@/assets/curso/tema1/61.png' alt='AvatarTop')
        .row.p-3
          h4.mb-1 Las salsas
          p.mb-4 Son el elemento lquido del plato, adems de aportar sabor contribuye a unificar los diferentes elementos del plato.

      .tarjeta.color-acento-contenido.color12
        .row.justify-content-center
          .col-12
            img(src='@/assets/curso/tema1/62.png' alt='AvatarTop')
        .row.p-3
          h4.mb-1 La decoracin
          p.mb-5 No es obligatorio, pero aporta originalidad.
          .row.mt-4
    .row.justify-content-center
      .col-lg-10
        p Por otra parte, en cuanto a las reglas que deben tenerse en cuenta, cada cocinero puede encontrar sus propias presentaciones, pero en general se considera que todas deben responder a las siguientes reglas:
        .row.justify-content-center
          .col-lg-10
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 <strong>Equilibrio:</strong> implica una bsqueda de armona entre los diferentes elementos del plato, desde formas a colores y sabores, etc.
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 <strong>Unidad:</strong> se busca que los diferentes elementos se unan de un modo cohesionado y elegante.

              li 
                i.fas.fa-angle-right.ss
                p.mb-0 <strong>Punto focal:</strong> es el rea que llama en un primer momento la atencin del consumidor.

              li 
                i.fas.fa-angle-right.ss
                p.mb-0 <strong>Flujo:</strong> es una sensacin sutil que parece dar vida al plato jugando con todos los elementos anteriores.
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 <strong>Altura:</strong> gracias a la altura podemos jugar con los relieves y el movimiento en el plato.
          
          .col-lg-2
            img.azx(src='@/assets/curso/tema1/12.svg' alt='AvatarTop')
    p.mt-4 A continuacin, encontrar descrita estas reglas para una mejor apreciacin de cmo llevar a cabo el servicio:
    .tarjeta.tarjeta--azul.p-4.mb-5.mt-5.bg7
      SlyderA.tema1-6(tipo="b")
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Reglas y tipos de emplatado
            p.mt-5 Para comenzar, tenemos que tener en cuenta que para emplatar se puede jugar con todas estas variables:
            ul.lista-ul
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Colores   

              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Texturas      

              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Alturas      
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Guarniciones       
              li 
                i.fas.fa-angle-right.ss
                p.mb-0 Salsas 
            p Y vajillas para colocar los alimentos, de una manera armnica, tenemos que ver el plato, como si fuera un reloj, tal como vemos en la imagen:
            p.mb-0 Si el plato es caliente, poner la carne a las a las 6 y el resto de componentes del plato a las 3 y a las 9, si es fro el plato, comenzar por el centro y desde ah seguir con el montaje.

        
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t1.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Diferentes tcnicas de emplatado
            p.mt-5 <strong>Emplatado tradicional:</strong> en este emplatado se divide el plato como si se tratase de un reloj. La protena se coloca a las 6, la guarnicin de carbohidratos a las 2 y la de verduras a las 10. Se trata de un emplatado rpido y sencillo que deja a la vista todos los ingredientes.

          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t2.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado NO tradicional
            p.mt-5 Aqu entra a formar parte la creatividad del cocinero, dejaremos las normas clsicas y nos vamos a utilizar la imaginacin y la vena artstica del chef, aparece con la <i>Nouvelle Cuisine</i> en Francia.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t3.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado estructurado o centrado
            p.mt-5 Se caracteriza por la aparicin de elementos decorativos y el cuidado de los detalles y aunque tengamos libertad, esta forma de emplatar se subdivide en dos categoras: estructurado y disperso
            p Todos los elementos se unen formando un conjunto y, vamos a jugar con la altura y el volumen, en la base, la guarnicin, encima la protena o el ingrediente principal, rodeando a la torre, la salsa y la decoracin por encima de los alimentos.

          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t4.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado disperso
            p.mt-5 Ni nmeros ni alturas, los ingredientes se integran por todo el plato, lo habitual es que el ms importante o principal vaya en el centro y el resto a su alrededor. Se busca la armona horizontal, todos los elementos lucen por s mismos y se aprecian texturas y tcnicas Este es el primer paso para aprender a emplatar, nos quedan ms captulos, los elementos y la composicin sern los siguientes captulos.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t5.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado simtrico
            p.mt-5 El plato se divide en dos partes y los elementos se colocan manteniendo un equilibrio proporcional entre ellas. 
            p Existe igualdad de peso en las dos partes del plato, como alas de mariposa. Transmite una sensacin de orden y armona procedente de la misma naturaleza.

          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t6.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 El emplatado asimtrico
            p.mt-5 Al igual que en el caso anterior, dividimos el plato en dos partes, sin embargo, una tiene ms peso visual que la otra.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t7.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado rtmico
            p.mt-5 Se emplea mucho en los postres y la pastelera. Se trata de hacer repeticiones en el plato de los diferentes elementos para conseguir un diseo dinmico y atractivo aunque ordenado.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t8.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado triangular o romboidal
            p.mt-5 Al utilizar este tipo de emplatado conseguimos que el ojo se pasee por todo el plato. Tambin permite jugar con las diferentes alturas.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t9.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado cuadrado u horizontal
            p.mt-5 Se utilizan los elementos en forma de cubos o formas similares.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t10.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Emplatado circular
            p.mt-5 El punto focal en este caso se sita en el centro del plato y los diferentes elementos se disponen a su alrededor.
          .col-md-6.mt-5   
              img(src='@/assets/curso/tema1/t11.png', alt='Texto que describa la imagen')
    .row.justify-content-center.mt-4
      .col-lg-10.col2.p-4
        .row
          .col-lg-1
            img.tamimg(src='@/assets/curso/tema1/13.svg', alt='Texto que describa la imagen')
          .col-lg-9
            h5.mt-4 Puede consultar ms acerca de este interesante tema sobre el emplatado, en el siguiente enlace:


