              a.boton.me-3.indicador__container(
                :href="('https://www.youtube.com/watch?v=02Brgs60BiY&t=8s')"
                target="_blank"
                type="application/pdf"
                @mouseover="mostrarIndicador = false"
              )
                span Ingresar
                i.fas.fa-link
                .indicador--click(v-if="mostrarIndicador")
