    #t_1_7.titulo-segundo.color-acento-contenido
      h2 1.7	 Menaje
    .row.justify-content-center
      .col-lg-4
        .row.justify-content-center
          .col-lg-12
            .cajon.color-acento-botones.p-1
              p.p-3 Cada restaurante tiene unas necesidades diferentes con relacin a los utensilios que requiere para poder crear sus preparaciones y para servirlas a sus clientes; esto normalmente depende del presupuesto disponible, del tipo de pblico al que se apunta y la imagen que se desea transmitir a los comensales.

          p.p-3.mb-0 Cuando se habla entonces de menaje, se hace referencia a los utensilios utilizados en la presentacin de los alimentos: la vajilla empleada para servir y presentar los platos y bebidas a los comensales, vasos, copas, platos y <i>bowls</i>, cubiertos, entre otros. Con lo anterior, se puede decir que el mensaje no se resume solo a los utensilios utilizados dentro de la cocina, sino tambin a los que salen a las mesas y los que se utilizan en las barras. 
      .col-lg-4
        img(src='@/assets/curso/tema1/ttt.png', alt='Texto que describa la imagen')
      .col-lg-4.mt-2
        p Este tema juega un rol primordial ya que es la carta de presentacin del establecimiento. Una vez se ha creado la experiencia para el comensal, la esttica y la presentacin de los platos, tienen un papel protagnico a la hora de generar una percepcin agradable y una recompra en el servicio. La vajilla, por ejemplo, es uno de los factores que ms genera diferenciacin entre los restaurantes y recordacin en un comensal, quien normalmente no consume solo el alimento, sino que se percata tambin del cubierto que se utiliza, el material de los platos, el color, los diseos etc. 
        p.mb-0 Los clientes esperan mucho ms que un buen servicio, buscan vivir una experiencia nica por lo que cada utensilio de la mesa juega un papel relevante.
    .row.mb-5.justify-content-center.mt-4
      .col-lg-8.mb-5.mb-lg-0
        //- .bloque-texto-b debe ir acompaado de una de una de estas clases => 
        //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones

        .bloque-texto-b.color-secundario.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h4.mb-0 Recuerde que la diferencia reside en la originalidad del diseo, la cual cada vez est ms alineada con los parmetros de calidad y buen gusto 
            i.fas.fa-quote-right    
      
    p Son varios los factores que influyen en una experiencia positiva con respecto al menaje, a continuacin, encontrar descrito cada uno de estos y su injerencia en la presentacin del plato, y el gusto e inters de los comensales
    .row.justify.content-center.mt-4
      .col-lg-8
        LineaTiempoD.color-acento-botones
          p(numero="1" titulo="Innovacin") En este aspecto se debe considerar el tipo de restaurante, tipo de comensal y funcionalidad de la vajilla. Se puede ofrecer una vajilla comercial que sea segura y que funcione con cualquier comida y cualquier decoracin o una vajilla completamente innovadora y nica, que brinde una ventaja competitiva.
          
          p(numero="2" titulo="Ambiente") Se debe tener en cuenta el tipo de sensacin que se desea crear con la decoracin de su restaurante y la manera en que se desea que los clientes perciban el establecimiento. El menaje adecuado ser de gran ayuda para asegurar el estilo de su negocio.
          p(numero="3" titulo="Durabilidad") Es esencial considerar qu tan duradero es el servicio de mesa. Si tiene un restaurante grande con plazos de entrega rpidos, necesitar invertir en vajillas comerciales que sean mucho ms duraderas que las que se necesitaran para un pequeo restaurante de estilo boutique donde el nmero de invitados es menor y la rotacin o reposicin de los utensilios ser ms progresiva. 
          
          p(numero="4" titulo="Materiales") Se debe definir entre los diferentes materiales que existen para el menaje, teniendo en cuenta que cada utensilio puede contribuir a la generacin de valor de la marca. Existe la cermica, la porcelana, la melamina, el barro, etc. 
          p(numero="5" titulo="Caractersticas del menaje") <strong>Platos:</strong> de pan, de presentacin, trincheros, soperos, de postre, de consom (tambin pueden ser tazas) <br><br> <strong>Copas (segn tipo de bebida):</strong> agua, vino blanco, vino tinto champn, <i>vermouth</i>, jerez <i>coac</i>, licor. <br><br><strong>Vasos:</strong> bsico, tubo, <i>highBall</i>, <i>pilsner</i>, chupito <br><br> <strong>Cubiertos (puede variar segn estilo/especializacin del restaurante):</strong> cuchara sopera, tenedor y cuchillo de mesa, tenedor y cuchillo de trinchar, cuchillo de carne, tenedor y pala de pescado, pinzas de marisco, cuchara salsera, cuchara, tenedor y cuchillo de postre, cortador y pala de tartas. <br><br><strong>Utensilios de cocina:</strong> cucharas y cucharones, de acero inoxidable, juego de cuchillos de cocina y tijeras, tablas de cortar batera completa de sartenes y cacerolas, incluyendo ollas de presin,cazos y cestillos, <i>bowls</i> de diferentes tamaos, biberones para salsas, bandejas, moldes para hornear, brochetas, esptulas, colador.
 
