      h2 1.8	 Tipos de servicio en un restaurante
    p Entendemos por tipos de servicio las diferentes formas en las que podemos hacer llegar el alimento preparado a la persona que lo va a degustar, pudiendo ser ste el cliente de un restaurante, o cualquier miembro familiar que se siente en la mesa para comer.
    p Los principales tipos de servicio son: emplatado, inglesa, y <i>bufet</i>, cada uno de ellos con unas caractersticas especficas que lo hacen idneo en funcin de las consideraciones oportunas que determinan la naturaleza del servicio. En el siguiente vdeo se explica con ms detalle las caractersticas de cada uno de estos tipos de servicio:
    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/4iYlPsNTcGY" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
