      h2 1.9	 Mercadeo digital para restaurantes
    .row.justify-content-center
      .col-lg-6
        img(src='@/assets/curso/tema1/67.png', alt='Texto que describa la imagen')

      .col-lg-6.mt-2
        p El mercadeo es el conjunto de tcnicas y estudios que buscan mejorar la comercializacin de un producto a partir de la identificacin de las necesidades de los consumidores; al hablar puntualmente de restaurantes, este trmino se refiere a todas las acciones y estrategias (tanto <i>offline</i> como <i>online</i>) que buscan fidelizar al cliente, mejorar la relacin con l, llegar a nuevos consumidores, promover los productos, aumentar ventas y diferenciar a la compaa.
        p En la actualidad, se debe prestar gran importancia al mercadeo digital ya que el Internet es el canal por excelencia para llegar a una mayor cantidad de clientes. La mayora de los usuarios hoy da, usan su celular para reservar su mesa en restaurantes y una vez visitado el establecimiento, comparten opiniones que llevan a una ms alta reputacin digital del establecimiento. Las tendencias son claras: todos los negocios estn migrando hacia una mayor virtualizacin de sus servicios, as que el sector gastronmico no se puede quedar atrs. 
        .row.justify-content-center
          .col-lg-12
            .cajon.color-acento-botones.p-4
              h5 Fidelizacin
              p.mb-0 Atraer y fidelizar comensales son de los retos ms grandes que enfrenta la industria gastronmica, y el mercadeo digital es un aliado importante en este proceso.
    p.mt-4 Las estrategias de <i>marketing</i>, se utilizan con el propsito de conectar a los consumidores con la empresa, en este caso el restaurante, a nivel de los productos que se ofrecen o que se pueden llegar a ofrecer en el futuro.   No basta con dar a conocer lo que queremos vender, sino que es necesario crear planteamientos de venta con perspectiva de futuro, tomando en cuenta los canales disponibles y jugando de acuerdo a sus dinmicas. Este es el motivo por el que cada sector tiene estrategias de <i>marketing</i> especficas orientadas a cubrir sus necesidades y peculiaridades. En el siguiente recurso, podr enterarse de las principales condiciones a tener en cuenta cuando se plantea una estrategia de mercado para restaurantes, veamos:
    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/FH4Vv68uxCQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    p Otra de las indicaciones establecidas dentro de esta forma de ofrecimiento del producto, son las plataformas de reserva <i>online</i>. Aunque an existen personas que prefiere llamar para reservar una mesa en un restaurante, el uso <i>online</i> crece a medida que la edad del comensal disminuye, por ello, es importante aplicar diferentes estrategias a la hora de generar las reservas para un establecimiento gastronmico:
    .row.justify-content-center
      .col-lg-4
        .row.colort.p-4
          h3.mt-4 Econmica
          p.mt-4 Las ms econmica pero no tan funcional: se trata de colocar un formulario en la <i>web</i> del restaurante que servir para obtener los datos del cliente, sin embargo, se requiere de alguien que est encargado de lo que sucede en la <i>web</i> para recibir dichos formularios a tiempo, ya que esta opcin no permite confirmar la reserva al momento, ni permite cierta conectividad.
      
      .col-lg-4
        img(src='@/assets/curso/tema1/68.png', alt='Texto que describa la imagen')

      .col-lg-4
        .row.color2.p-4
          h3.mt-4 Mejor opcin
          p.mt-4 La mejor opcin: <i>software</i> de reservas asociado a un canal de venta. Esta es la mejor opcin para restaurantes que desean difusin y notoriedad <i>online</i>. Los canales de venta, son a la vez guas y buscadores de restaurantes que te traern nuevos clientes, pero tambin cobrarn su porcentaje cada que un comensal haga una reserva.
    p.mt-4 Entre las ventajas de las plataformas de reservas <i>online</i> se encuentran las siguientes:
    ul.lista-ul
      li 
        i.fas.fa-angle-right.ss
        p.mb-0 Captar nuevos clientes: con ellas se puede impactar a un pblico ms amplio de manera que muchos clientes nuevos, podrn conocer y visitar el restaurante. 
 
      li 
        i.fas.fa-angle-right.ss
        p.mb-0 El uso del telfono se reduce: los empleados del restaurante podrn ejercer sus tareas sin necesidad de preocuparse por contestar el telfono y atender las reservas.
     
      li 
        i.fas.fa-angle-right.ss
        p.mb-0 No tiene horarios: los clientes pueden hacer su reserva en cualquier da y horario, sin importar si el local est abierto o cerrado.

      li 
        i.fas.fa-angle-right.ss
        p.mb-0 Recordatorios de reserva: estas plataformas siempre envan notificaciones a los clientes, por lo que stos no olvidarn su cita con tu restaurante. 
    p.mt-4 Algunos ejemplos de plataformas de reserva <i>online</i> pueden observarse a continuacin:        
    h3.mt-5  Plataformas de reserva <i>online</i>
    .row.t1.mt-5
      .col-lg-4.grupo1.p-5
        .row.mt-5
        .row.mt-5
        p.mt-4 Comprueba las reservas en tiempo real con tan solo un clic.<br>Recibe una notificacin por cada nueva reserva.<br>Crea distintas cuentas de usuario para empleados concretos.<br>Registra todos los cambios en las reservas.
      .col-lg-4.grupo2.p-5
        .row.mt-5
        .row.mt-5
        p.mt-4 El restaurante debes tener su perfil en Google My Business.
        p Esta aplicacin recomienda restaurantes cercanos al lugar desde donde el cliente hace la bsqueda. 
        p Hace su propia valoracin de los mejores locales. Para ello tiene en cuenta las opiniones de los clientes y la informacin ms completa y actualizada que haya proporcionado el propietario del restaurante.
      .col-lg-4.grupo3.p-5
        .row.mt-5
        .row.mt-5
        p.mt-4 Esta plataforma es casi una comunidad, donde todos los usuarios cuelgan sus opiniones de las diferentes experiencias que han vivido.

        p Cuando se acumulan las opiniones de los clientes y si en su gran mayora son positivas, TripAdvisor proporciona al restaurante una recomendacin como el mejor restaurante del ao segn los usuarios. Lo que hace que an ms clientes se decidan a probar las delicias de las que todo el mundo habla.


    p.text-center Nota. https://bit.ly/3kYCXW4
