      h2 1.10	 Costos para restaurantes
    p Calcular el costo de los alimentos es muy importante para un restaurante, principalmente porque permite tener claridad sobre el margen de utilidad y rentabilidad del negocio, adems de establecer el precio de los alimentos y bebidas. A continuacin, encontrar informacin ampliada al respecto, adems de invitar a consultar el artculo de Morillo Moreno, M (2009). Costos del servicio de alimentos y bebidas en establecimientos de alojamientos tursticos Visin Gerencial, nm. 2, pp. 304-327 Universidad de los Andes Mrida, Venezuela. <a href="https://www.redalyc.org/pdf/4655/465545881013.pdf">https://www.redalyc.org/pdf/4655/465545881013.pdf</a>  en su seccin 2, encontrar explicacin a este tema tan interesante. 
